// Fonts import
@import "fonts.less";

// Animation import
@import "animate.less";

// Import core
@import "bootstrap.less";

// Common styles
html,
body {
  height: 100px;
}
body {
  background: #d5d5d5;
  font-family: @font-family-serif;
}
.container {
  width: 950px !important;
}

// Navbar
.navbar {
  background: #2b303e;
  border-radius: 0;

  .navbar-brand {
    .img-retina("../img/logo-speaktoit.png"; "../img/logo-speaktoit@2x.png"; 156px; 40px);
    text-indent: -9999px;
    .size(156px; 40px);
    margin-top: 21px;
  }

  .navbar-text {
    border-left: 1px solid #fff;
    font: 300 30px/27px 'Open Sans', sans-serif;
    color: #fffefe;
    padding-left: 15px;
    margin: 22px 0 27px 15px;
  }
}

// Main block
.main-block {
  background: #fff;

  .breadcrumb {
    background: none;
    font-weight: 700;
    font-size: 24px;
    width: 888px;
    margin: 25px auto 35px;
    padding: 0;

    li {
      color: #2b303e;

      + li {
        &::before {
          content: '>';
          color: #2b303e;
        }
      }

      a {
        color: #0488da;
      }
    }
  }

  .ipad {
    background: url(../img/bg-ipad.jpg) no-repeat;
    .size(888px; 502px);
    margin: 0 auto 24px;
    padding: 32px 73px 33px;
    position: relative;

    .ipad-table {
      display: table;
      .size(100%; 365px);
      margin: 0;

      &.high {
        height: 437px;
      }

      .ipad-cell {
        display: table-cell;
        vertical-align: middle;
      }
    }

    .ipad-scroll {
      .size(100%; 365px);
      overflow: auto;
    }

    .ipad-table-list {
      table-layout: fixed;
      width: 100%;

      tr {
        + tr {
          td {
            border-top: 1px solid #434754;
          }
        }

        &:nth-child(odd) {
          td {
            background: #313644;
          }
        }

        &:nth-child(even) {
          td {
            background: #2b303e;
          }
        }
      }

      td {
        color: #89949b;
        font-size: 18px;
        vertical-align: middle;
        padding: 5px 8px;

        &.title {
          font-size: 24px;
          color: #fff;
        }

        &.info {
          span {
            + span {
              margin-left: 12px;
            }
          }
        }

        .btn {
          .button-variant(#fff; #0488da; #0488da);
          border: none;
          border-radius: 50%;
          .square(70px);
          line-height: 70px;
          padding: 0;

          + .btn {
            margin-left: 10px;
          }

          .fa {
            font-size: 30px;
            vertical-align: middle;
          }
        }
      }
    }

    .mic-bottom-wrap {
      background: #242936;
      border: 1px solid #1c202c;
      line-height: 72px;
      text-align: center;
      height: 72px;
      position: absolute;
      right: 73px;
      bottom: 33px;
      left: 73px;

      .fa {
        font-size: 36px;
        color: #fff;
        vertical-align: middle;
      }
    }

    .speak {
      h1 {
        color: #89949b;
        font-size: 24px;
        font-weight: 400;
        line-height: 30px;
        margin: 0;
        text-align: center;
      }

      .mic-wrap {
        position: relative;
        text-align: center;
        line-height: 164px;
        .square(164px);
        margin: 28px auto 0;
      }

      .fa {
        font-size: 36px;
        color: #fff;
        vertical-align: middle;
      }

      .circle1,
      .circle2,
      .circle3 {
        border-radius: 50%;
        position: absolute;
      }

      .circle1 {
        border: 1px solid #414552;
        .square(164px);
        top: 0;
        left: 0;
      }

      .circle2 {
        border: 1px solid #80838b;
        .square(136px);
        top: 13px;
        left: 13px;
      }

      .circle3 {
        border: 1px solid #d5d6d8;
        .square(108px);
        top: 13px;
        left: 13px;
      }
    }

    .talk {
      .ipad-cell {
        text-align: center;

        h1,
        h2 {
          font-size: 30px;
          font-weight: 400;
          line-height: 30px;
          margin: 0;
        }

        h1 {
          color: #ed7524;
          
        }

        h2 {
          color: #feffff;
          margin-top: 30px;
        }
      }
    }

    .navigation {
      .ipad-table {
        width: 50%;
        float: left;

        .ipad-cell {
          padding: 0 40px;
        }
      }

      .direction {
        background: #16a085;
        border-radius: 50%;
        text-align: center;
        line-height: 70px;
        .square(70px);

        .fa {
          font-size: 30px;
          color: #feffff;
          vertical-align: middle;
        }
      }

      h1 {
        color: #ffffff;
        font-size: 36px;
        font-weight: 400;
        margin: 30px 0 19px;
      }

      .data {
        color: #89949b;
        font-size: 18px;
        margin: 0;

        span {
          + span {
            margin-left: 20px;
          }
        }
      }
    }

    .contacts-calling {
      text-align: center;

      h1 {
        color: #ffffff;
        font-size: 36px;
        line-height: 1;
        font-weight: 400;
        margin: 20px 0 10px;
      }

      .status {
        color: #89949b;
        font-size: 18px;
        line-height: 1;
        font-weight: 400;
        margin: 0 0 55px;
      }

      .btn {
        .button-variant(#fff; #ec7063; #ec7063);
        border: none;
        border-radius: 50%;
        line-height: 70px;
        padding: 0;
        .square(70px);

        .fa {
          font-size: 30px;
          .rotate(135deg);
          vertical-align: middle;
        }
      }
    }

    .music-listen {
      text-align: center;

      h1 {
        color: #ffffff;
        font-size: 36px;
        font-weight: 400;
        line-height: 1;
        margin: 20px 0 10px;
      }

      h2 {
        color: #89949b;
        font-size: 18px;
        font-weight: 400;
        line-height: 1;
        margin: 0;
      }
    }

    .main {
      text-align: center;

      h1 {
        color: #feffff;
        font-size: 30px;
        font-weight: 400;
        line-height: 1;
        margin: 0 0 74px;
      }

      .menu {
        a {
          display: inline-block;
          color: #feffff;
          font-size: 14px;

          + a {
            margin-left: 34px;
          }

          &:hover {
            .icon {
              background: darken(#0488da, 10%);
            }
          }

          .icon {
            background: #0488da;
            border-radius: 50%;
            display: block;
            line-height: 70px;
            .square(70px);
            margin: 0 auto 10px;

            .fa {
              font-size: 30px;
              vertical-align: middle;
            }
          }
        }
      }
    }
  }
}

// Response
.response {
  padding: 40px 0 138px;

  ul {
    list-style: none;
    width: 592px;
    padding: 0;
    margin: 0 auto;

    li {
      padding-left: 24px;
      position: relative;

      + li {
        margin-top: 20px;
      }

      &:before {
        content: '';
        border-left: 1px solid #c4c4c4;
        .size(0; 70px);
        position: absolute;
        left: 0;
        top: 30px;
      }

      &.you {
        p {
          &.text {
            &:before {
              background: #0488da;
            }
          }
        }
      }

      &.autodemo {
        p {
          &.text {
            &:before {
              background: #ed7524;
            }
          }
        }
      }

      p {
        margin: 0;

        &.title {
          color: #2b303e;
          font-size: 12px;
          font-weight: 700;
          line-height: 30px;
        }

        &.text {
          color: #8e8e8e;
          font-size: 14px;
          position: relative;

          &:before {
            content: '';
            border: 6px solid #d5d5d5;
            border-radius: 50%;
            .square(19px);
            margin-top: -10px;
            position: absolute;
            top: 50%;
            left: -33px;
          }

          strong {
            margin-right: 9px;
          }
        }
      }
    }
  }
}

// Form
.form {
  background: #e6e6e6;
  margin-top: -95px;
  padding: 25px 0;
  position: relative;

  .form-group {
    width: 620px;
    margin: 0 auto;
    position: relative;

    &:before {
      content: '';
      border-left: 1px solid #c4c4c4;
      .size(0; 25px);
      position: absolute;
      top: -25px;
      left: 14px;
    }

    &.open {
      .btn {
        border-color: #7aa4ea !important;
      }
    }

    input[type=text] {
      outline: none !important;
      box-shadow: none !important;
      border: 2px solid #ddd;
      border-radius: 0;
      font-size: 14px;
      color: #89949b;
      .size(397px; 45px);
      padding: 10px 15px;
      float: left;
    }

    .btn,
    input[type=submit] {
      outline: none !important;
      border-width: 2px;
      border-style: solid;
      height: 45px;
      font-weight: 700;
      font-size: 12px;
      text-transform: uppercase;
    }

    .btn {
      .button-variant(#fff; #b4b7b9; #b4b7b9);
      border-radius: 0 22px 22px 0;
      float: left;
      width: 105px;
    }

    input[type=submit] {
      .button-variant(#fff; #0488da; #0488da);
      border-radius: 22px;
      float: right;
      width: 93px;
    }

    .dropdown-menu {
      box-shadow: 0 0 2px rgba(0,0,0,.2);
      border-radius: 8px;
      top: auto;
      bottom: 101%;
      padding: 24px 18px;
      width: 502px;

      li {
        font-size: 14px;

        + li {
          margin-top: 20px;
        }

        a {
          color: #89949b;
          white-space: normal;
        }
      }
    }
  }
}
